/* --- GLOBAL STYLES (MATCHING OLD HTML) --- */
:root {
  --accent: #007bff;
  --section-bg: #ffffff;
  --card-bg: #ffffff;
  --dynamic-text: #333333;
}

body { 
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
  margin: 0; 
  line-height: 1.6; 
  color: var(--dynamic-text); 
  background-color: var(--section-bg);
  scroll-behavior: smooth;
  transition: background-color 0.8s ease, color 0.8s ease;
}

/* --- NAVIGATION (OLD GRADIENT + NEW FIXED LOGIC) --- */
nav { 
  position: fixed; /* Keeps it moving while scrolling */
  top: 0; 
  width: 100%; 
  padding: 20px 0;
  text-align: center; 
  z-index: 1000;
  /* Original HTML Gradient */
  background: linear-gradient(to bottom, rgba(0,0,0,0.4), transparent);
  transition: all 0.4s ease-in-out;
}

/* Glass effect when scrolling */
nav.scrolled {
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(10px);
  padding: 12px 0;
}

nav a { 
  color: white; 
  text-decoration: none; 
  margin: 0 15px; 
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.85rem;
  letter-spacing: 1px;
}

/* --- HERO SECTION (RESTORED OLD SPACING/SHADOWS) --- */
header { 
  background-size: cover;
  background-position: center;
  background-attachment: fixed; /* Original parallax-like effect */
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  transition: background-image 0.8s ease-in-out;
}

header h1 { 
  font-size: clamp(2.5rem, 8vw, 4rem); 
  margin: 0; 
  text-shadow: 2px 2px 15px rgba(0,0,0,0.6); 
}

.profile-circle {
  width: 170px;
  height: 170px;
  border-radius: 50%;
  border: 4px solid white;
  object-fit: cover;
  margin: 20px 0;
  box-shadow: 0 8px 25px rgba(0,0,0,0.5);
}

header p { 
  font-size: 1.4rem; 
  margin: 0; 
  font-weight: 300;
  text-shadow: 1px 1px 8px rgba(0,0,0,0.7); 
}

/* --- DYNAMIC CONTENT SECTIONS --- */
section { 
  padding: 100px 20px; 
  max-width: 1000px; 
  margin: auto; 
}

.grid { 
  display: grid; 
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); 
  gap: 25px; 
}

.card { 
  background-color: var(--card-bg);
  color: var(--dynamic-text);
  border: 1px solid rgba(0,0,0,0.05); 
  padding: 30px; 
  border-radius: 15px; 
  box-shadow: 0 5px 15px rgba(0,0,0,0.05);
  transition: background-color 0.8s ease;
}

.accent-link { color: var(--accent); text-decoration: none; font-weight: bold; }

/* --- BUTTONS --- */
.button-group {
  display: flex;
  gap: 15px;
  margin-top: 35px;
}

.btn {
  display: inline-block;
  padding: 12px 30px;
  border-radius: 50px;
  font-weight: bold;
  text-decoration: none;
  transition: all 0.3s ease;
}

.btn-primary { background: var(--accent); color: white; }
.btn-outline { background: transparent; color: white; border: 2px solid white; }
.btn-outline:hover { background: white; color: #333; }

footer { text-align: center; padding: 50px; background: #111; color: #777; }


/* --- PHONE OPTIMIZATIONS --- */
@media (max-width: 600px) {
  header {
    /* Critical for mobile scaling */
    background-attachment: scroll !important; 
    background-size: cover;
    background-position: center center;
    min-height: 100vh; /* Ensures it fills the phone screen */
  }

  nav {
    padding: 15px 0;
    /* Keeping the fixed behavior but making it solid for readability */
    background-color: rgba(0, 0, 0, 0.9);
  }
  
  nav a {
    display: inline-block;
    margin: 5px 10px;
    font-size: 0.75rem;
  }

  header h1 {
    font-size: 2.2rem;
    width: 90%;
  }

  .profile-circle {
    width: 140px;
    height: 140px;
  }

  .button-group {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .btn {
    width: 85%; 
    text-align: center;
  }
}